大音乐现场，到晚上OPPOReno浙江卫视2019年中音乐盛典的盛大开启，OPPOReno造乐节掀起了一场原创歌手与万千乐迷以音乐为载体的全天候狂欢。"),_c('br')]),_vm._v(" "),_c('img',{attrs:{"src":require("../../assets/img/phone08.jpg"),"alt":""}}),_vm._v(" "),_c('p',[_vm._v("　　作为OPPO Reno造乐节的发起方，OPPO希望通过这场音乐盛会，唤醒更多年轻原创音乐人的创作激情，鼓励更多优秀原创音乐走入大众，为华语乐坛积蓄力量、沉淀经典。在未来，OPPO也将继续以实际行动助力原创音乐发展，与行业专业力量一起为原创音乐提供更多支持与鼓励。"),_c('br')])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"hot_titleBox"},[_c('span',{staticClass:"hot_title"},[_vm._v("热门推荐")])])}]
